@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*{
  margin: 0;
  color: #333;
  font-family: "Quicksand";
}
body{
  display: block;
  background-color: #eee;
}
.bg-photo-over{
  background: rgba(0,0,0,.26);
  background-size: cover;
  height: 480px;
  position: absolute;
  width: 100%;
  z-index: 2;
  margin-top: 90px;
}
.bg-photo-container{
  height: 480px;
  overflow: hidden;
  position: absolute;
  width: 100%;
  z-index: 1;
  margin-top: 90px;
}
.bg-photo{
  background: #eee url(/blog-react/static/media/Pile-of-books-1.5e46fc37.jpg);
  -webkit-filter: blur(0px);
          filter: blur(0px);
  height: calc(100% + 2 * 0px);
  left: 0px;
  position: absolute;
  top: 0px;
  width: calc(100% + 2 * 0px);
}
.navbar{
padding: 20px;
display:-webkit-flex;
display:flex;
-webkit-align-items: center;
        align-items: center;
width: auto;
max-width: 800px;
margin:0 auto;
position: fixed;
border-bottom: 2px solid #f1356d;
z-index: 1000;
right: 0;
left: 0;
}

.container-fluid{
 float: left;
 margin-left: auto;

}
.logo{
color: #f1356d;
text-decoration: none;
font-weight: bold;
font-size: 30px;
}
ul{
  list-style: none;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
  font-weight: bold;
}
.nav-item{
  float: left;
  margin-left: auto;
  display: block;

}
.nav-link{
  text-decoration: none;
  margin-left:20px;
}
.add-one{
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
.nav-link:hover {
  color: #f1356d;
}

.loading h1{
  color: #fff;
  font-weight: bold;
  font-size: 30px;
  position: absolute;
  top: 100px;
  left: auto;
}
.error h2{
  color: #fff;
  font-weight: bold;
  font-size: 20px;
  position: absolute;
  top: 100px;
  left: auto;
}
.content {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
}
.page-body{
  z-index: 20;
  position: relative;
}
.page-body .center{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 0 auto;
  max-width: 922px;
  min-height: 100vh;
  padding: 24px 0;
  box-sizing: border-box;
}

.container{
  display: block;
  position: relative;
  
}
.blog-list h1{
  color: #fff;
  margin-left:auto;
  margin-bottom: 10px;
  font-size: 40px;
  margin-top: 80px;

}
.blogs{
  width: auto;
  height: auto;
  background-color: #ffff;
  padding: 10px;
  margin: 20px 0;
  border-radius: 20px;
}
.blogs h1{
  font-size: 20px;
}
.blogs p{
  font-size: 15px;
  color: #777;
}
.blogs p span{
  color: #f1356d;
}
.blogs a{
  text-decoration: none;
}
.container button{
  bottom: 0;
  position: absolute;
}
.blog-details{
  padding: 10px;
  top: 90px;
  position: relative;
  display: -webkit-flex;
  display: flex;
  margin: 0 auto;
  max-width: 922px;
}
.blog-details span{
  color: #f1356d;
  position: absolute;
  bottom: 0;
  margin-top: 10px;
}
.blog-details h2{
  font-size: 30px;
}
.blog-details button{
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  position: absolute;
  bottom: -60px;
}
.blog-body{
  padding: 10px;
  margin:10px 0;
  max-width: 900px;
  font-size: 18px;
}
/*create new one*/
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
  padding: 10px;
  top: 90px;
  position: relative;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
/*Not found page*/
.not-found{
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
  padding: 10px;
  top: 90px;
  position: relative;
  font-size: 20px;
}
.not-found p{
  margin: 20px 0;
}
.not-found a{
  text-decoration: none;
  color: #f1356d;
  font-weight: bold;
}
@media screen and (max-width: 600px) {
  .navbar{
    overflow: hidden;
  }
  .navbar .nav-link{
    display: none;
  }
  .page-body .center {
    max-width: 300px;
  }
  .container,.blogs{
    margin-bottom: 20px;
    margin-top: 10px;
    max-width: 400px;
  }
  .blog-list h1{
    font-size: 30px;
  }

}
